// Components

// Buttons
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 24px;
  height: 40px;
  border-radius: 20px;
  font-family: var(--md-sys-typeface-brand);
  font-weight: 500;
  font-size: 14px;
  cursor: pointer;
  border: none;
  @include transition(background-color 0.2s, box-shadow 0.2s);

  &.btn-primary {
    background-color: var(--md-sys-color-primary);
    color: var(--md-sys-color-on-primary);

    &:hover {
      box-shadow: var(--md-sys-elevation-level-1);
      // Simplify hover state with opacity or overlay logic ideally, but direct tint for now
      opacity: 0.92;
    }
  }

  &.btn-outlined {
    background-color: transparent;
    border: 1px solid var(--md-sys-color-outline);
    color: var(--md-sys-color-primary);

    &:hover {
      background-color: rgba(var(--md-sys-color-primary), 0.08); // Need RGB values for this usually
      // Simplified fallback
      background-color: var(--md-sys-color-surface-variant);
    }
  }
}

// Cards
.card {
  background-color: var(--md-sys-color-surface);
  border-radius: var(--md-sys-shape-corner-medium);
  padding: 16px;
  margin-bottom: 16px;
  border: 1px solid var(--md-sys-color-outline);
  // Material 3 cards can be elevated, filled, or outlined.

  &.elevated {
    box-shadow: var(--md-sys-elevation-level-1);
    border: none;
  }

  &.filled {
    background-color: var(--md-sys-color-surface-variant);
    border: none;
  }
}

// Navigation
.nav-list {
  list-style: none;
  padding: 0;
  margin: 0;

  &.nested {
    padding-left: 16px; // Indentation for nested levels
    // Optional: add a border line on the left for visual hierarchy
    border-left: 1px solid var(--md-sys-color-outline-variant);
    margin-left: 12px;
  }
}

.nav-item {
  margin-bottom: 4px;

  details > summary {
    list-style: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;

    // Hide default marker
    &::-webkit-details-marker {
      display: none;
    }

    .nav-arrow {
      fill: var(--md-sys-color-on-surface-variant);
      transition: transform 0.2s ease;
    }
  }

  details[open] > summary .nav-arrow {
    transform: rotate(180deg);
  }
}

.nav-link {
  display: block;
  padding: 12px 16px;
  border-radius: 28px; // Full pill shape
  color: var(--md-sys-color-on-surface-variant);
  font-weight: 500;
  text-decoration: none;
  font-size: 14px;
  line-height: 20px;

  &:hover {
    background-color: var(--md-sys-color-surface-variant);
  }

  &.active {
    background-color: var(--md-sys-color-secondary-container);
    color: var(--md-sys-color-on-secondary-container);
    font-weight: 700;
  }
}

// Search
.sidebar-search {
  padding: 0 0 16px 0;

  .search-wrapper {
    position: relative;
    width: 100%;
  }

  .search-icon {
    position: absolute;
    left: 12px;
    top: 50%;
    transform: translateY(-50%);
    color: var(--md-sys-color-on-surface-variant);
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none; // Let clicks pass through to input
  }

  input {
    width: 100%;
    padding: 10px 16px 10px 44px; // Left padding for icon
    border-radius: 28px;
    border: 1px solid var(--md-sys-color-outline);
    background-color: var(--md-sys-color-surface-container-high);
    color: var(--md-sys-color-on-surface);
    font-family: inherit;
    font-size: 14px;
    box-sizing: border-box;
    height: 40px;

    &:focus {
      outline: 2px solid var(--md-sys-color-primary);
      border-color: transparent;
    }

    &::placeholder {
      color: var(--md-sys-color-on-surface-variant);
    }
  }

  .search-results {
    margin-top: 8px;
    background: var(--md-sys-color-surface-container-high);
    border-radius: 8px;
    overflow: hidden;

    // Only show when has content
    &:empty {
      display: none;
    }

    a {
      display: block;
      padding: 8px 12px;
      color: var(--md-sys-color-on-surface);
      text-decoration: none;
      font-size: 13px;
      border-bottom: 1px solid var(--md-sys-color-outline-variant);

      &:last-child {
        border-bottom: none;
      }

      &:hover {
        background-color: var(--md-sys-color-surface-variant);
      }
    }
  }
}

// Tables
table {
  width: 100%;
  border-collapse: collapse;
  margin: 16px 0;
  background-color: var(--md-sys-color-surface);

  th, td {
    padding: 12px 16px;
    text-align: left;
    border-bottom: 1px solid var(--md-sys-color-outline);
  }

  th {
    font-weight: 500;
    color: var(--md-sys-color-on-surface);
    background-color: var(--md-sys-color-surface-variant);
  }
}
