// Global Variables

// Fonts
$font-family-base: 'Roboto', sans-serif;
$font-family-code: 'Roboto Mono', monospace;

// Font Weights
$font-weight-regular: 400;
$font-weight-medium: 500;
$font-weight-bold: 700;

// Spacing Unit
$spacing-unit: 8px;

// Breakpoints
$mobile-breakpoint: 768px;
$tablet-breakpoint: 1024px;
